export default {
	data() {
		return {
			windowW:'',
			banner:{
				indicatorDots: true,
				circular: true,
				indicatorColor: "rgb(221,221,221)",
				indicatorActiveColor: "rgba(201,0,10,1)",
				autoplay: false,
				interval: 2000,
				duration: 500,
				previousMargin:'',
				nextMargin:'',
			},
			banner_list:[],
		}
	},
	created(){
		var _this=this;
	},
	mounted(){
		var _this=this;
		uni.getSystemInfo({
			success: function (res) {
				_this.windowW=res.windowWidth;
				_this.banner.previousMargin=_this.windowW*30/375;
				_this.banner.nextMargin=_this.windowW*20/375;
			}
		});
		
		//baner
		_this.getBanner();	
		
	},
	methods: {
		supermarket() {
			uni.navigateTo({
			    url: 'supermarket/supermarket',
			});
		},
		
		//baner
		getBanner:function(){
			let _this=this;
			let data={
				id:5,
			};
			_this.$axios(_this.$baseUrl.banner,data).then(res =>{
				if(res.data.status==1){
					_this.banner_list=res.data.result.banner;
				}
			}).catch(error =>{
				
			})
			
		}
	}
}
